import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ButtonModule } from 'primeng/button';

@Component({
  selector: 'pla-button-fon',
  imports: [ButtonModule],
  templateUrl: './pla-button-fon.component.html',
  styleUrl: './pla-button-fon.component.css'
})
export class PlaButtonFonComponent {
  @Input() label: string = '';
  @Output() onSave = new EventEmitter<void>();

  onSaveClick() {
    this.onSave.emit();
  }
}
